<template>
  <div id='clustersetting'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/clustersetting'}">系统设置</el-breadcrumb-item>
      <el-breadcrumb-item>NLB集群管理</el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>

    <el-main>
    <el-row :gutter='8'>
      <el-col :span="12" style="font-size: 14px; text-align: left;">
        <el-button @click='handleInit' round>初始化设置</el-button>
      </el-col>
      <el-col :span="12" style="font-size: 14px; text-align: right;">
        <el-button type="success" @click='handleExp' round>添加机器</el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column prop="hostname" label="节点名称" width=200></el-table-column>
      <el-table-column prop="role"     label="节点角色"></el-table-column>
      <el-table-column prop="ip"       label="IP地址"></el-table-column>
      <el-table-column prop="status" label="运行状态">
        <template slot-scope="scope">
          <span :style="{color: scope.row.status[1]}">{{ scope.row.status[0] }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="operation"     label="操作">
        <template slot-scope="scope">
          <!--
          <el-tooltip content="转换角色" placement="top">
            <i class="fa el-icon-caret-top" @click="handleSetMaster(scope.row)" v-if="scope.row.role=='MINION'"></i>
            <i class="fa el-icon-caret-bottom" @click="handleSetMinion(scope.row)" v-if="scope.row.role=='MASTER'"></i>
          </el-tooltip>
          <span>|</span> -->
          <el-tooltip content="踢出集群" placement="top">
            <i class="fa el-icon-delete" @click="hanldeKickoff(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="为NLB集群添加机器" :visible.sync="dialogClusterExp">
      <span>说明：</span>
      <div style="font-size: 12px; line-height: 0.8;">
        <p >1.请输入扩容的机器IP列表；</p>
        <p >2.确保每台机器可通过“初始化配置”中的登录方式登录访问。</p>
        <p >3.在初始化成功后，记得要将新增的机器挂到集群上游的负载均衡设备。</p>
      </div>
      <el-form :model="clusterExpForm" ref="clusterExpForm">
        <el-form-item label="IP列表：" prop="ip_list">
          <el-input type="textarea" :rows="8" v-model="clusterExpForm.ip_list" placeholder="请填入新机器的IP列表，一行一个"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogClusterExp = false">取 消</el-button>
        <el-button type="primary" @click="confirmExp">提 交</el-button>
      </div>
    </el-dialog>

    <el-dialog title="初始化设置" :visible.sync="dialogInit">
      <span>说明：</span>
      <div style="font-size: 12px; line-height:1.1">
      <p>1.新建集群，或者往集群中添加新机器扩容时，新机器需要初始化。</p>
      <p>2.初始化将安装saltstack，设置主机名（可选），安装其他必要的软件包，调整系统参数等。请确保每台机器配置了正确的yum源，可以yum安装saltstack组件。</p>
      <p>4.已初始化成功的机器，将不会再执行初始化。</p>
      <p>3.请提供一个<b style='color: red'>统一的系统账户</b>，可通过ssh使用“用户名/密码”登录</p>
      </div>
      <el-form :model="initSettingForm" ref="initSettingForm">
        <el-form-item label="系统用户" prop="sysuser" label-width="110px" :rules="[{ required: true, message: '请输入操作系统用户ID！'}]">
          <el-input v-model="initSettingForm.sysuser" placeholder="请输入可通过ssh登录的系统用户ID"></el-input>
        </el-form-item>
        <el-form-item label="系统登录密码" prop="syspasswd" label-width="110px" :rules="[{ required: true, message: '请输入操作系统用户登录密码！'}]">
          <el-input v-model="initSettingForm.syspasswd" placeholder="系统用户对应的登录密码"></el-input>
        </el-form-item>
        <el-form-item label="是否设置主机名" prop="sethostname" label-width="110px">
          <el-select v-model="initSettingForm.sethostname" placeholder="建议选择“是”，以生成格式统一的节点名称" style="width:100%;">
            <el-option v-for="item in sethostnameOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="主机名前缀" prop="hostnameprefix" label-width="110px">
          <el-input v-model="initSettingForm.hostnameprefix" placeholder="主机名将会被设置成以下格式：<前缀>-<数字编号>"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogInit = false">取 消</el-button>
        <el-button type="primary" @click="confirmInitSet">提 交</el-button>
      </div>
    </el-dialog>

    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      clusterExpForm: {
        ip_list: null,
      },
      initSettingForm:{
        sysuser: null,
        syspasswd: null,
        sethostname: null,
        hostnameprefix: 'NLB-CLUSTER'
      },
      sethostnameOptions: [{value: 'Y', label: '是'}, {value: 'N', label: '否'}],
      dialogClusterExp: false,
      dialogInit: false,
      initSettingEditable: false,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/cluster',
        method: 'post',
        data: null,
      }
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let config = {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/cluster',
        method: 'get',
        params: {},
      };
      service.request(config).then(data => {
        this.tableData = data.data;
        }, err => {
        this.$alert(err.message);
        });
     },

    handleExp () {
      this.clusterExpForm = {ip_list: null};
      this.dialogClusterExp = true;
    },
    confirmExp () {
      this.postConfig.data = this.clusterExpForm;
      this.postConfig.data.action = 'expandCluster';
      service.request(this.postConfig)
      .then(data => {
        this.$message({message: '机器添加成功', type: 'success'});
        this.dialogClusterExp = false;
        this.getData();
      }, err => {
        this.$message({message: err.message, type: 'error'});
      });
    },

    getInitConfig() {
      this.postConfig.data = {'action':'getInit'};
      service.request(this.postConfig).then(data => {
        this.initSettingForm = data.initSetting;
      }, err =>{this.alert(err.message);});
    },
    handleInit() {
      this.dialogInit = true;
      this.getInitConfig();
    },
    confirmInitSet() {
      this.postConfig.data = this.initSettingForm;
      this.postConfig.data.action = 'setInit';
      service.request(this.postConfig).then(data => {
        this.$message({message: '初始化设置成功',type: 'success'});
        this.dialogInit = false;
      }, err => {
        this.$message({message: err.message, type: 'error'});
      });
    },

    hanldeKickoff(row) {
      this.$confirm(`确定要将机器ip: ${row.ip}从集群中移除？`,"警告",{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {ip: row.ip, action: 'kickoffNode'};
        service.request(this.postConfig).then(data => {
          this.$message({message: '移除成功', type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },

    /* handleSetMaster(row) {
      this.$confirm(`确定要将机器(ip: ${row.ip})设置为master节点？`,"警告",{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {ip: row.ip, action: 'setMaster'};
        service.request(this.postConfig).then(data => {
          this.$message({message: '设置成功，请等待各节点连接新的master', type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },
    handleSetMinion(row) {
      this.$confirm(`确定要将机器(ip: ${row.ip})设置为minion节点？`,"警告",{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {ip: row.ip, action: 'setMinion'};
        service.request(this.postConfig).then(data => {
          this.$message({message: '设置成功，请等待节点master功能的移除', type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    }, */

  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



