<template>
  <div id='users'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>
    <el-main>
    <el-row :gutter='8'>
      <el-col :span="24" style="font-size: 14px; text-align: right;">
        <el-button type='success' @click='handleAdd' round>添加用户</el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column prop="username"    label="用户ID"></el-table-column>
      <el-table-column prop="realname"    label="姓名"></el-table-column>
      <el-table-column prop="email"    label="邮箱"></el-table-column>
      <el-table-column prop="group"      label="属组"></el-table-column>
      <el-table-column prop="operation"     label="操作">
        <template slot-scope="scope">
          <el-tooltip content="修改用户信息" placement="top">
            <i class="el-icon-edit"   @click="handleEdit(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="重置密码" placement="top">
            <i class="el-icon-view"   @click="handleResetPW(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="删除用户" placement="top">
            <i class="el-icon-delete" @click="handleDelete(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="readableAction" :visible.sync="dialogVisible">
      <el-form :model="formData" ref="formData" label-width="100px">
        <el-form-item label="用户ID" prop="username" :rules="[{ required: true, message: '请输入用户ID，只能由字母、数字组成'}]">
          <el-input v-model="formData.username"  placeholder="用户ID只能由字母、数字组成" :readonly='usernameReadOnly'></el-input>
        </el-form-item>
        <el-form-item label="属 组" prop="group" :rules="[{ required: true, message: '请为有用户分配属组'}]">
          <el-select v-model="formData.group" placeholder="属组决定了用户的权限" style='width: 100%'>
            <el-option v-for="item in groupOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="初始密码" prop="passwd" :rules="[{ required: true, message: '请为用户设置初始密码'}]" v-if="this.action == 'add'">
          <el-input v-model="formData.passwd"  placeholder="设置一个8位以上的初始密码。用户登录后，可自行修改自己的密码"></el-input>
        </el-form-item>
        <el-form-item label="姓 名" prop="realname">
          <el-input v-model="formData.realname" placeholder="用户的真实姓名"></el-input>
        </el-form-item>
        <el-form-item label="邮 箱" prop="email">
          <el-input v-model="formData.email" placeholder="用户的邮件地址，用以接受系统通知邮件"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmAdd" v-if="action=='add'">提 交</el-button>
        <el-button type="primary" @click="confirmEdit" v-if="action=='edit'">提 交</el-button>
      </div>
    </el-dialog>

    <el-dialog title="重置用户密码" :visible.sync="resetPWVisible">
      <el-form :model="formResetPW" ref="formResetPW" label-width="100px">
        <el-form-item label="用户ID" prop="username" :rules="[{ required: true, message: '请输入用户ID，只能由字母、数字组成'}]">
          <el-input v-model="formResetPW.username"  placeholder="用户ID只能由字母、数字组成" readonly></el-input>
        </el-form-item>
        <el-form-item label="初始密码" prop="newPasswd" :rules="[{ required: true, message: '请为用户设置新的密码'}]">
          <el-input v-model="formResetPW.newPasswd"  placeholder="设置一个8位以上的初始密码。用户登录后，可自行修改自己的密码"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetPWVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmResetPW">提 交</el-button>
      </div>
    </el-dialog>

    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      formData: {
        username: null,
        passwd: null,
        realname: null,
        email: null,
        group: null,
      },
      formResetPW:{
        username: null,
        newPasswd: null,
      },
      action: null,
      dialogVisible: false,
      groupOptions: null,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/users',
        method: 'post',
        data: null,
      },
      readableAction: '添加用户',
      resetPWVisible: false,
      usernameReadOnly: false,
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // api请求的baseURL, 用于DataSourceGateWay
        baseURL: '/proxy/api/v1',
        // 请求参数以jquery.param方式进行序列化
        paramSerializerJQLikeEnabled: true,
        url: '/users',
        // to methods of that instance.
        method: 'get',
        // params仅用于get请求， 会拼接在url后面
        params: params
      };
      service.request(config).then(data => {
        // console.log(data);
        this.tableData = data.data;
        this.groupOptions = data.groups
        }, err => {
        this.$alert(err.message);
        });
     },

    resetForm () {
      this.formData = {
        username: null,
        passwd: null,
        realname: null,
        email: null,
        group: null,
      }
    },

    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.readableAction = '添加用户';
      this.usernameReadOnly = false;
      this.dialogVisible = true;
    },
    confirmAdd () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '添加失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleDelete (row) {
          this.$confirm(`确定要删除这个用户：${row.username} ?`, '警告', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.postConfig.data = {username: row.username, action: 'delete'};
            service.request(this.postConfig)
            .then(data => {
              if (data.result == 'SUCCESS') {
                this.$message({
                  message: '删除成功',
                  type: 'success'
                });
                this.getData();
              } else {
                this.$message({
                  message: data.message,
                  type: 'error'
                });
              }
            }, err => {
              this.$alert(err.message);
            });
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
    },

    handleEdit(row){
      this.resetForm();
      Object.keys(this.formData).forEach(k => {
        this.formData[k] = row[k];
      });
      this.action = 'edit';
      this.readableAction= '修改用户信息';
      this.usernameReadOnly = true;
      this.dialogVisible = true;
    },
    confirmEdit () {
    this.postConfig.data = this.formData;
    this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleResetPW(row){
      this.formResetPW.username = row.username;
      this.formResetPW.newPasswd = null;
      this.action = 'resetPasswd';
      this.resetPWVisible = true;
    },
    confirmResetPW() {
      this.postConfig.data = this.formResetPW;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '密码重置成功',
            type: 'success'
          });
          this.resetPWVisible = false;
        } else {
          this.$message({
            message: data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },


  }
}
</script>

<style>
</style>



