<template>
  <div id='blacklist'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/iplist'}">访问控制</el-breadcrumb-item>
      <el-breadcrumb-item>黑名单策略</el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>
    <el-main>
    <el-row :gutter='8'>
      <el-col :span='10'>
        <el-input v-model="searchForm.search_value" placeholder="搜索策略名称，域名，添加用户，描述"  @keyup.enter.native="searchData"></el-input>
      </el-col>
      <el-col :span="6" style="font-size: 14px">
        <el-button @click="searchData" type="primary" icon="el-icon-search">搜 索</el-button>
      </el-col>
      <el-col :span="8" style="font-size: 14px; text-align: right;">
        <el-button type='success' @click='handleAdd' round>添加黑名单</el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column prop="id"                label="ID" width=50></el-table-column>
      <el-table-column prop="strategy_name"     label="策略名称"></el-table-column>
      <el-table-column prop="proxy_domain_name" label="所属代理"></el-table-column>
      <el-table-column prop="description"       label="描述"></el-table-column>
      <el-table-column prop="status"            label="运行状态">
        <template slot-scope="scope">
          <span :style="{color: scope.row.status[1]}">{{ scope.row.status[0] }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="operation"         label="操作">
        <template slot-scope="scope">
          <el-tooltip content="修改" placement="top">
            <i class="el-icon-edit"   @click="handleEdit(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="停用" placement="top" v-if="scope.row.status[1]!='gray'" >
            <i class="fa fa-pause" @click="handledisable(scope.row)"></i>
          </el-tooltip>
          <el-tooltip content="启用" placement="top" v-if="scope.row.status[1]=='gray'" >
            <i class="fa fa-play" @click="handlenable(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="重新下发" placement="top">
            <i class="fa fa-repeat" @click="repush(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="查看访问IP列表" placement="top">
            <i class="fa fa-file-text-o" @click="showIPList(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="查看日志" placement="top">
            <i class="fa fa-file-o" @click="showLog(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="删除" placement="top">
            <i class="el-icon-delete" @click="handleDelete(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加黑名单策略" :visible.sync="dialogVisible">
      <span style="color: red;">警告：成功添加后，将对选择的应用代理<b>立即启用</b>黑名单策略，请结合业务<b>谨慎</b>添加！</span>
      <el-form :model="formData" ref="formData" label-width="100px">
        <el-form-item v-show="false">
          <el-input v-model="formData.id"></el-input>
        </el-form-item>
        <el-form-item label="策略名称" prop="strategy_name" :rules="[{ required: true, message: '请输入策略名称'}]">
          <el-input v-model="formData.strategy_name"  placeholder="请输入策略名称"></el-input>
        </el-form-item>
        <el-form-item label="所属代理" prop="proxy_domain_name" :rules="[{ required: true, message: '请选择一个七层代理'}]">
          <el-select v-model="formData.proxy_domain_name" placeholder="请选择要启用此黑名单策略的七层代理" style="width:100%;">
            <el-option v-for="item in allProxies" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="访问IP列表" prop="iplist" :rules="[{ required: true, message: '请选择加入此黑名单的IP'}]">
          <el-transfer :titles="['访问IP池', '已选择的IP']" v-model="formData.iplist" :data="ipListPool"></el-transfer>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input type="textarea" :rows="4" v-model="formData.description" placeholder="请对此黑名单策略做简单描述"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmAdd" v-if="action=='add'">提 交</el-button>
        <el-button type="primary" @click="confirmEdit" v-if="action=='edit'">提 交</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="popUpTitle" :visible.sync="popUpDialog">
      <el-input auto-complete="off" type="textarea" :rows="22" readonly='readonly' v-model="logContent.logs"></el-input>
    </el-dialog>

    <el-dialog :title="CheckIPList" :visible.sync="checkIPListShow">
      <el-input auto-complete="off" type="textarea" :rows="22" readonly='readonly' v-model="IPListString"></el-input>
    </el-dialog>


    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      mysearch: '',
      searchForm: {
        search_value: null,
      },
      formData: {
        id:  null,
        strategy_name: null,
        proxy_domain_name: null,
        iplist: [],
        description: null,
      },
      action: 'add',
      dialogVisible: false,
      popUpDialog: false,
      popUpTitle: null,
      checkIPList: null,
      checkIPListShow: false,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/blacklist',
        method: 'post',
        data: null,
      },
      logContent: {
        logs: null,
      },
      allProxies: null,
      ipListPool: [],
      IPListString: null,
    }
  },
  mounted() {
    this.getData();
    this.getProxiesAndIPs();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // api请求的baseURL, 用于DataSourceGateWay
        baseURL: '/proxy/api/v1',
        // 请求参数以jquery.param方式进行序列化
        paramSerializerJQLikeEnabled: true,
        url: '/blacklist',
        // to methods of that instance.
        method: 'get',
        // params仅用于get请求， 会拼接在url后面
        params: params
      };
      service.request(config).then(data => {
          // console.log(data);
          this.tableData = data.data;
          let refresh = 0;
          for(let obj of this.tableData){
            if(obj.status[1] == 'orange'){
              refresh = 1;
              break;
            }
          };
          if(refresh == 1) {
            setTimeout(() => {this.getData()},10000);
          };
        }, err => {
        this.$alert(err.message);
        });
     },

     searchData () {
       let params = {search_value: this.searchForm.search_value};
       let config = {
         baseURL: '/proxy/api/v1',
         paramSerializerJQLikeEnabled: true,
         url: '/blacklist',
         method: 'get',
         params: params
       };
       service.request(config).then(data => {
         // console.log(data);
         this.tableData = data.data;
         }, err => {
         this.$alert(err.message);
         });
      },

    resetForm () {
      this.formData = {
        id:  null,
        strategy_name: null,
        proxy_domain_name: null,
        iplist: [],
        description: null,
      }
    },

    handleAdd () {
      this.resetForm();
      this.action = 'add';
      //this.getProxiesAndIPs();
      this.dialogVisible = true;
    },
    confirmAdd () {
      this.postConfig.data = this.formData;
      this.postConfig.data.iplist = this.postConfig.data.iplist.join('/');
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '添加失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleEdit(row){
      this.resetForm();
      this.action = 'edit';
      //this.getProxiesAndIPs();
      Object.keys(this.formData).forEach(k => {
        this.formData[k] = row[k];
      });
      this.dialogVisible = true;
    },
    confirmEdit () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      this.postConfig.data.iplist = this.postConfig.data.iplist.join('/');
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '修改失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    warningAndAction(row, confirmMessage, action, successMessage) {
      this.$confirm(confirmMessage,"警告",{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {id: row.id, action: action};
        service.request(this.postConfig).then(data => {
          this.$message({message: successMessage, type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },

    handleDelete(row){
        let confirmMessage = `删除黑名单：${row.strategy_name}？`;
        let action = 'delete';
        let successMessage = '删除成功';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    handledisable(row){
        let confirmMessage = `确定要停用黑名单${row.strategy_name}？`;
        let action = 'disable';
        let successMessage = '已停用';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    handlenable(row){
        let confirmMessage = `确定要重新启用黑名单${row.strategy_name}？`;
        let action = 'enable';
        let successMessage = '已启用';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    repush(row){
        let confirmMessage = `将黑名单${row.strategy_name}重新加入配置下发队列？`;
        let action = 'saltRepush';
        let successMessage = '成功加入队列';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    showLog(row){
        this.postConfig.data = {id: row.id, action: 'getPushLog'};
        service.request(this.postConfig).then(data => {
          this.logContent.logs = data.logs;
          this.popUpTitle = `黑名单${row.strategy_name}的saltstack推送日志`
          this.popUpDialog = true;
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
    },

    showIPList(row){
      this.IPListString = row.iplist.join('\n');
      this.checkIPList = `黑名单${row.strategy_name}的IP列表`
      this.checkIPListShow = true;
    },

    getProxiesAndIPs(){
        this.postConfig.data = {action: 'getProxiesAndIPs'};
        service.request(this.postConfig).then(data => {
          this.allProxies = data.proxies;
          data.ips.forEach((ip, index) => {
           this.ipListPool.push({label: ip, key: ip});
          });
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
    },
  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



