<template>
  <div id='tcpproxy'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/httpproxy'}">应用配置</el-breadcrumb-item>
      <el-breadcrumb-item>四层代理</el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>
    <el-main>
    <el-row :gutter='8'>
      <el-col :span='10'>
        <el-input v-model="searchForm.search_value" placeholder="请将鼠标悬停“搜索”按钮，查看使用帮助"  @keyup.enter.native="searchData"></el-input>
      </el-col>
      <el-col :span="6" style="font-size: 14px">
        <el-popover ref="searchHelp" placement="top-start" title="搜索使用说明" width="400" trigger="hover" open-delay=1000>
          <div style="font-size: 10px">
          <p>1. 全字段模糊搜索，支持以下字段的模糊匹配：域名(DOMAIN，包含代理域名和其他域名)，应用(APP)，应用端口(APP_PORT)，监听端口(LISTEN_PORT)，服务器(SERVER，包含应用服务器和热份服务器)，用户(USER)，描述(DESC)。</p>
          <p>2. 指定字段进行模糊匹配：DOMAIN:<域名>&APP:<应用>... </p>
          <p>3. 指定字段进行精确匹配：DOMAIN=<域名>&APP=<应用>... </p>
          <p>4. 输入格式不满足第二、三条时，将触发第一条的全字段模糊搜索。</p>
          </div>
        </el-popover>
        <el-button @click="searchData" type="primary" icon="el-icon-search" v-popover:searchHelp>搜 索</el-button>
      </el-col>
      <el-col :span="8" style="font-size: 14px; text-align: right;">
        <el-button type='success' @click='handleAdd' round>添加应用</el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="right" inline class="demo-table-expand">
            <el-form-item label="代理域名：">
              <span>{{ props.row.proxy_domain_name }}</span>
            </el-form-item>
            <el-form-item label="代理应用：">
              <span>{{ props.row.app_name }}</span>
            </el-form-item>
            <el-form-item label="监听端口：">
              <span>{{ props.row.proxy_port }}</span>
            </el-form-item>
            <el-form-item label="应用端口：">
              <span>{{ props.row.app_port }}</span>
            </el-form-item>
            <el-form-item label="应用服务器：">
              <span v-for="server in props.row.app_servers.split('/')">
                <p style='line-height: 10px'>{{ server }}</p>
              </span>
            </el-form-item>
            <el-form-item label="热备服务器：">
              <span v-for="server in props.row.app_servers_backup.split('/')">
                <p style='line-height: 10px'>{{ server }}</p>
              </span>
            </el-form-item>
            <el-form-item label="添加时间：">
              <span>{{ props.row.add_time }}</span>
            </el-form-item>
            <el-form-item label="添加用户：">
              <span>{{ props.row.add_user }}</span>
            </el-form-item>
            <el-form-item label="描述：">
              <span>{{ props.row.description }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column prop="id"    label="ID" width=50></el-table-column>
      <el-table-column prop="proxy_domain_name"    label="代理域名" width=200></el-table-column>
      <el-table-column prop="app_name"      label="代理应用"></el-table-column>
      <el-table-column prop="app_port"    label="应用端口"></el-table-column>
      <el-table-column prop="status"      label="运行状态">
        <template slot-scope="scope">
          <span :style="{color: scope.row.status[1]}">{{ scope.row.status[0] }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="operation"     label="操作">
        <template slot-scope="scope">
          <el-tooltip content="修改" placement="top">
            <i class="el-icon-edit"   @click="handleEdit(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="停用" placement="top" v-if="scope.row.status[1]!='gray'" >
            <i class="fa fa-pause" @click="handledisable(scope.row)"></i>
          </el-tooltip>
          <el-tooltip content="启用" placement="top" v-if="scope.row.status[1]=='gray'" >
            <i class="fa fa-play" @click="handlenable(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="重新下发" placement="top">
            <i class="fa fa-repeat" @click="repush(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="查看日志" placement="top">
            <i class="fa fa-file-o" @click="showLog(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="删除" placement="top">
            <i class="el-icon-delete" @click="handleDelete(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加一个四层代理" :visible.sync="dialogVisible">
      <el-form :model="formData" ref="formData" label-width="100px">
        <el-form-item v-show="false">
          <el-input v-model="formData.id"></el-input>
        </el-form-item>
        <el-form-item label="代理域名" prop="proxy_domain_name" :rules="[{ required: true, message: '请输入一个合法域名'}]">
          <el-input v-model="formData.proxy_domain_name"  placeholder="请输入一个合法域名"></el-input>
        </el-form-item>
        <el-form-item label="应用名称" prop="app_name" :rules="[{ required: true, message: '请输入代理应用的名称'}]">
          <el-input v-model="formData.app_name"  placeholder="请输入代理应用的名称"></el-input>
        </el-form-item>
        <el-form-item label="应用端口" prop="app_port" :rules="[{ required: true, message: '请输入应用的服务端口'}]">
          <el-input v-model="formData.app_port" placeholder="请输入代理应用的服务端口"></el-input>
        </el-form-item>
        <el-form-item label="监听端口" prop="proxy_port" :rules="[{ required: true, message: '请输入代理端口，注意保持端口唯一'}]">
          <el-input v-model="formData.proxy_port" placeholder="请输入代理端口，注意保持端口唯一"></el-input>
        </el-form-item>
        <el-form-item label="应用服务器" prop="app_servers" :rules="[{ required: true, message: '请输入代理应用所在服务器IP'}]">
          <el-input v-model="formData.app_servers" placeholder="请输入代理应用所在服务器IP，多个用'/'隔开"></el-input>
        </el-form-item>
        <el-form-item label="热备服务器" prop="app_servers_backup">
          <el-input v-model="formData.app_servers_backup" placeholder="如有，请输入代理应用运行的热备服务器，多个用'/'隔开"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input type="textarea" :rows="4" v-model="formData.description" placeholder="请对此代理配置做简单描述"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmAdd" v-if="action=='add'">提 交</el-button>
        <el-button type="primary" @click="confirmEdit" v-if="action=='edit'">提 交</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="popUpTitle" :visible.sync="popUpDialog">
      <el-input auto-complete="off" type="textarea" :rows="22" readonly='readonly' v-model="logContent.logs"></el-input>
    </el-dialog>

    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      mysearch: '',
      searchForm: {
        search_value: null,
      },
      formData: {
        id:  null,
        proxy_domain_name: null,
        app_name: null,
        app_port: null,
        app_servers:null,
        app_servers_backup: null,
        proxy_port: null,
        description: null,
      },
      action: 'add',
      dialogVisible: false,
      popUpDialog: false,
      popUpTitle: null,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/tcpproxy',
        method: 'post',
        data: null,
      },
      logContent: {
        logs: null,
      },
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // api请求的baseURL, 用于DataSourceGateWay
        baseURL: '/proxy/api/v1',
        // 请求参数以jquery.param方式进行序列化
        paramSerializerJQLikeEnabled: true,
        url: '/tcpproxy',
        // to methods of that instance.
        method: 'get',
        // params仅用于get请求， 会拼接在url后面
        params: params
      };
      service.request(config).then(data => {
          // console.log(data);
          this.tableData = data.data;
          let refresh = 0;
          for(let obj of this.tableData){
            if(obj.status[1] == 'orange'){
              refresh = 1;
              break;
            }
          };
          if(refresh == 1) {
            setTimeout(() => {this.getData()},10000);
          };
        }, err => {
        this.$alert(err.message);
        });
     },

     searchData () {
       let params = {search_value: this.searchForm.search_value};
       let config = {
         baseURL: '/proxy/api/v1',
         paramSerializerJQLikeEnabled: true,
         url: '/tcpproxy',
         method: 'get',
         params: params
       };
       service.request(config).then(data => {
         // console.log(data);
         this.tableData = data.data;
         }, err => {
         this.$alert(err.message);
         });
      },

    resetForm () {
      this.formData = {
        id:  null,
        proxy_domain_name: null,
        app_name: null,
        app_port: null,
        app_servers:null,
        app_servers_backup: null,
        proxy_port: null,
        description: null,
      }
    },

    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.dialogVisible = true;
    },
    confirmAdd () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '添加失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleEdit(row){
      this.resetForm();
      this.action = 'edit';
      Object.keys(this.formData).forEach(k => {
        this.formData[k] = row[k];
      });
      this.dialogVisible = true;
    },
    confirmEdit () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '修改失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    warningAndAction(row, confirmMessage, action, successMessage) {
      this.$confirm(confirmMessage,"警告",{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {id: row.id, action: action};
        service.request(this.postConfig).then(data => {
          this.$message({message: successMessage, type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },

    handleDelete(row){
        let confirmMessage = `删除四层代理：${row.proxy_domain_name}？`;
        let action = 'delete';
        let successMessage = '删除成功';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    handledisable(row){
        let confirmMessage = `确定要停用${row.proxy_domain_name}的四层代理？`;
        let action = 'disable';
        let successMessage = '已停用';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    handlenable(row){
        let confirmMessage = `确定要重新启用${row.proxy_domain_name}的四层代理？`;
        let action = 'enable';
        let successMessage = '已启用';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    repush(row){
        let confirmMessage = `将${row.proxy_domain_name}的四层代理重新加入配置下发队列？`;
        let action = 'saltRepush';
        let successMessage = '成功加入队列';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    showLog(row){
        this.postConfig.data = {id: row.id, action: 'getPushLog'};
        service.request(this.postConfig).then(data => {
          this.logContent.logs = data.logs;
          this.popUpTitle = `${row.proxy_domain_name}四层代理的saltstack推送日志`
          this.popUpDialog = true;
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
    },

  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



