<template>
  <div id='iplist'>
    <el-main>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/iplist'}">访问控制</el-breadcrumb-item>
      <el-breadcrumb-item>访问IP池</el-breadcrumb-item>
    </el-breadcrumb>
    </el-main>
    <el-main>
    <el-row :gutter='8'>
      <el-col :span='10'>
        <el-input v-model="searchForm.search_value" placeholder="搜索IP，描述，添加用户"  @keyup.enter.native="searchData"></el-input>
      </el-col>
      <el-col :span="6" style="font-size: 14px">
        <el-button @click="searchData" type="primary" icon="el-icon-search">搜 索</el-button>
      </el-col>
      <el-col :span="8" style="font-size: 14px; text-align: right;">
        <el-button type='success' @click='handleAdd' round>添加IP</el-button>
      </el-col>
    </el-row>

    <el-table height="470" style="width: 100%" :data="tableData" >
      <el-table-column prop="id"              label="ID" width=50></el-table-column>
      <el-table-column prop="ip"              label="访问IP" width=200></el-table-column>
      <el-table-column prop="add_time"        label="添加时间" width=220></el-table-column>
      <el-table-column prop="add_user"        label="添加人"></el-table-column>
      <el-table-column prop="description"     label="描述"></el-table-column>
      <el-table-column prop="operation"     label="操作">
        <template slot-scope="scope">
          <el-tooltip content="修改" placement="top">
            <i class="el-icon-edit"   @click="handleEdit(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="查看加入的白名单列表" placement="top">
            <i class="fa fa-file-text-o" @click="showWhiteList(scope.row)"></i>
          </el-tooltip>
          <span>|</span>
          <el-tooltip content="删除" placement="top">
            <i class="el-icon-delete" @click="handleDelete(scope.row)"></i>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加一个访问IP" :visible.sync="dialogVisible">
      <el-form :model="formData" ref="formData" label-width="100px">
        <el-form-item v-show="false">
          <el-input v-model="formData.id"></el-input>
        </el-form-item>
        <el-form-item label="访问IP" prop="ip" :rules="[{ required: true, message: '请输入一个IP'}]">
          <el-input v-model="formData.ip"  placeholder="请输入一个IP"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input type="textarea" :rows="4" v-model="formData.description" placeholder="请做简单描述，比如这个IP属于哪个商户，或者什么原因添加的这个访问IP"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmAdd" v-if="action=='add'">提 交</el-button>
        <el-button type="primary" @click="confirmEdit" v-if="action=='edit'">提 交</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="CheckWhiteList" :visible.sync="checkWhiteListShow">
      <el-input auto-complete="off" type="textarea" :rows="22" readonly='readonly' v-model="WhiteListString"></el-input>
    </el-dialog>
    </el-main>
  </div>
</template>

<script>
import DataProvider from '@bbfe/data-provider';

let service = new DataProvider();
export default {
  data() {
    return {
      tableData: [],
      mysearch: '',
      searchForm: {
        search_value: null,
      },
      formData: {
        id:  null,
        ip: null,
        description: null,
      },
      action: 'add',
      dialogVisible: false,
      postConfig: {
        baseURL: '/proxy/api/v1',
        paramSerializerJQLikeEnabled: true,
        url: '/iplist',
        method: 'post',
        data: null,
      },
      checkWhiteList: null,
      checkWhiteListShow: false,
      WhiteListString:null,
    }
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData () {
      let params = {};
      let config = {
        // api请求的baseURL, 用于DataSourceGateWay
        baseURL: '/proxy/api/v1',
        // 请求参数以jquery.param方式进行序列化
        paramSerializerJQLikeEnabled: true,
        url: '/iplist',
        // to methods of that instance.
        method: 'get',
        // params仅用于get请求， 会拼接在url后面
        params: params
      };
      service.request(config).then(data => {
          // console.log(data);
          this.tableData = data.data;
        }, err => {
        this.$alert(err.message);
        });
     },

     searchData () {
       let params = {search_value: this.searchForm.search_value};
       let config = {
         baseURL: '/proxy/api/v1',
         paramSerializerJQLikeEnabled: true,
         url: '/iplist',
         method: 'get',
         params: params
       };
       service.request(config).then(data => {
         // console.log(data);
         this.tableData = data.data;
         }, err => {
         this.$alert(err.message);
         });
      },

    resetForm () {
      this.formData = {
        id:  null,
        ip: null,
        description: null,
      }
    },

    handleAdd () {
      this.resetForm();
      this.action = 'add';
      this.dialogVisible = true;
    },
    confirmAdd () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '添加成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '添加失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    handleEdit(row){
      this.resetForm();
      this.action = 'edit';
      Object.keys(this.formData).forEach(k => {
        this.formData[k] = row[k];
      });
      this.dialogVisible = true;
    },
    confirmEdit () {
      this.postConfig.data = this.formData;
      this.postConfig.data.action = this.action;
      service.request(this.postConfig)
      .then(data => {
        console.log(data);
        if (data.result == 'SUCCESS') {
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.dialogVisible = false;
          this.getData();
        } else {
          this.$message({
            message: '修改失败：' + data.message,
            type: 'error'
          });
        }
      }, err => {
        this.$alert(err.message);
      });
    },

    warningAndAction(row, confirmMessage, action, successMessage) {
      this.$confirm(confirmMessage,"警告",{
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.postConfig.data = {id: row.id, action: action};
        service.request(this.postConfig).then(data => {
          this.$message({message: successMessage, type: 'success'});
          this.getData();
        }, err => {
          this.$message({message: err.message, type: 'error'});
        });
      });
    },

    handleDelete(row){
        let confirmMessage = `删除IP：${row.ip}？`;
        let action = 'delete';
        let successMessage = '删除成功';
        this.warningAndAction(row, confirmMessage, action, successMessage);
    },

    showWhiteList(row){
      this.WhiteListString = row.whitelists.join('\n');
      this.checkWhiteList = `${row.ip}加入的白名单列表`
      this.checkWhiteListShow = true;
    },

  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 100px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



